import {
    getComponentFolders,
    getComponentMeta,
    getComponentReadme,
    getCopFileUrl
} from '../api/componentApi';
import { saveLastSyncTime, getLastSyncTime } from '../utils/storage';

export class ComponentManager {
    constructor() {
        this.components = []; // 存储组件元信息列表
        this.isLoading = false; // 加载状态标记
    }

    // 初始化：加载组件列表
    async init() {
        this.setLoading(true);
        try {
            await this.loadComponents(); // 加载组件数据
            this.renderComponentList(); // 渲染列表
            this.bindEvents(); // 绑定事件
            this.updateLastSyncTime(); // 显示最后同步时间
        } catch (error) {
            this.renderError(`加载失败: ${error.message}`);
        } finally {
            this.setLoading(false);
        }
    }

    // 从本地/远程加载组件数据
    async loadComponents() {
        const folders = await getComponentFolders(); // 获取所有组件文件夹
        // 批量加载每个组件的meta信息
        this.components = await Promise.all(
            folders.map(async (folderName) => {
                const meta = await getComponentMeta(folderName);
                return { ...meta, folderName }; // 关联文件夹名称（用于后续加载详情）
            })
        );
    }

    // 同步组件列表（刷新数据）
    async syncComponents() {
        if (this.isLoading) return;
        this.setLoading(true);
        try {
            await this.loadComponents();
            await saveLastSyncTime(Date.now());
            this.updateLastSyncTime();
            this.renderComponentList();
        } catch (error) {
            alert(`同步失败: ${error.message}`);
        } finally {
            this.setLoading(false);
        }
    }

    // 更新最后同步时间显示
    async updateLastSyncTime() {
        const lastSync = await getLastSyncTime();
        const syncBtn = document.getElementById('sync-components');
        syncBtn.textContent = lastSync
            ? `最后同步: ${new Date(lastSync).toLocaleString()}`
            : '同步组件列表';
    }

    // 渲染组件列表
    renderComponentList() {
        const listEl = document.getElementById('componentList');
        if (!listEl) return;

        const searchTerm = document.getElementById('component-search').value.toLowerCase();
        // 搜索筛选
        const filtered = this.components.filter(component =>
            component.displayName.toLowerCase().includes(searchTerm) ||
            component.description?.toLowerCase().includes(searchTerm)
        );

        // 渲染列表项
        listEl.innerHTML = filtered.length
            ? filtered.map(component => this.createComponentCard(component)).join('')
            : '<div style="text-align: center; padding: 2rem;">暂无匹配组件</div>';
    }

    // 创建组件卡片HTML（仅显示基础信息和查看按钮）
    createComponentCard(component) {
        return `
      <div class="component-card" data-folder="${component.folderName}">
        <h3>${component.displayName}</h3>
        <p class="component-desc">${component.description || '无描述'}</p>
        <div style="font-size: 0.8rem; color: #666;">
          作者: ${component.author} | 主版本: ${component.version}
        </div>
        <button class="component-btn detail-btn" data-folder="${component.folderName}">
          查看版本分支
        </button>
      </div>
    `;
    }

    // 显示组件详情（版本分支+下载按钮+README）
    async showComponentDetail(folderName) {
        try {
            // 获取组件元信息和README
            const component = this.components.find(c => c.folderName === folderName);
            const readme = await getComponentReadme(folderName);

            // 创建模态框内容
            const modalHtml = `
        <div class="modal-content">
          <span class="close-btn">&times;</span>
          <h2>${component.displayName}</h2>
          <p>作者: ${component.author} | 主版本: ${component.version}</p>
          
          <h3>可用版本分支（点击下载）</h3>
          <div class="cop-branches">
            ${component.copNames.map(copName => `
              <a href="${getCopFileUrl(folderName, copName)}" 
                 download 
                 class="cop-download-btn">
                ${copName}
              </a>
            `).join('')}
          </div>
          
          <h3>组件说明</h3>
          <div class="component-readme">${this.formatReadme(readme)}</div>
        </div>
      `;

            // 显示模态框
            const modal = document.createElement('div');
            modal.className = 'modal';
            modal.innerHTML = modalHtml;
            document.body.appendChild(modal);
            modal.style.display = 'flex';

            // 绑定关闭事件
            modal.querySelector('.close-btn').addEventListener('click', () => {
                document.body.removeChild(modal);
            });
        } catch (error) {
            alert(`加载详情失败: ${error.message}`);
        }
    }

    // 安装组件（通过组件ID）- 修改后
    async installComponent(componentId) {
        const component = this.components.find(c => c.id === componentId);
        if (!component) {
            alert('组件不存在');
            return;
        }

        try {
            let downloadUrl;
            // 非核心组件需要获取下载链接
            if (!component.required) {
                const { success, data, message } = await getComponentDownloadUrl(componentId);
                if (!success || !data) {
                    throw new Error(message || '获取下载链接失败（链接为空）');
                }
                downloadUrl = data;
                console.log(`开始下载组件: ${downloadUrl}`);
            }

            // 执行文件下载（核心逻辑）
            if (downloadUrl) {
                const response = await fetch(downloadUrl);
                if (!response.ok) {
                    throw new Error(`下载失败: HTTP ${response.status}`);
                }

                // 检查文件大小（避免空文件）
                const contentLength = response.headers.get('content-length');
                if (!contentLength || parseInt(contentLength) <= 0) {
                    throw new Error('下载的文件为空');
                }

                // 处理文件下载
                const blob = await response.blob();
                const url = window.URL.createObjectURL(blob);
                const a = document.createElement('a');
                a.href = url;
                a.download = `${component.name}-v${component.version}.zip`; // 自定义文件名
                document.body.appendChild(a);
                a.click();
                window.URL.revokeObjectURL(url);
                document.body.removeChild(a);
            }

            // 添加到已安装列表（默认启用）
            this.installedComponents.push({ ...component, enabled: true });
            await saveInstalledComponents(this.installedComponents);
            this.renderComponentList();
            alert(`组件《${component.name}》安装成功`);
        } catch (error) {
            console.error('安装失败:', error);
            alert(`安装失败: ${error.message}`);
        }
    }

    // 简单格式化README（保留换行）
    formatReadme(text) {
        return text ? text.replace(/\n/g, '<br>') : '暂无说明文档';
    }

    // 绑定所有事件
    // 绑定事件（修改后）
    bindEvents() {
        const listEl = document.getElementById('componentList');
        const modal = document.getElementById('installModal');
        const closeBtn = modal?.querySelector('.close-btn');
        const confirmInstallBtn = document.getElementById('confirm-install');
        const componentUrlInput = document.getElementById('component-url');

        // 组件列表事件委托（安装/启用/禁用/卸载）
        listEl.addEventListener('click', async (e) => {
            if (e.target.classList.contains('install-btn')) {
                const componentId = e.target.dataset.id;
                // 打开模态框并填充默认ID（如果是已知组件）
                const component = this.components.find(c => c.id === componentId);
                if (component) {
                    componentUrlInput.value = component.id;
                }
                this.toggleModal(true);
            }

            if (e.target.classList.contains('enable-btn') || e.target.classList.contains('disable-btn')) {
                const componentId = e.target.dataset.id;
                await this.toggleComponentStatus(componentId);
            }

            if (e.target.classList.contains('uninstall-btn')) {
                const componentId = e.target.dataset.id;
                if (confirm('确定要卸载此组件吗？')) { // 添加确认提示
                    await this.uninstallComponent(componentId);
                }
            }
        });

        // 搜索和筛选事件
        document.getElementById('component-search').addEventListener('input', () => this.renderComponentList());
        document.getElementById('component-filter').addEventListener('change', () => this.renderComponentList());

        // 模态框关闭按钮事件
        closeBtn?.addEventListener('click', () => {
            this.toggleModal(false);
            componentUrlInput.value = ''; // 清空输入
        });

        // 点击模态框背景关闭
        modal?.addEventListener('click', (e) => {
            if (e.target === modal) { // 仅点击背景时关闭
                this.toggleModal(false);
                componentUrlInput.value = '';
            }
        });

        // 确认安装按钮事件
        confirmInstallBtn?.addEventListener('click', async () => {
            const inputValue = componentUrlInput.value.trim();
            if (!inputValue) {
                alert('请输入组件ID或URL');
                return;
            }

            // 这里可以根据输入值判断是ID还是URL，分别处理
            const isUrl = inputValue.startsWith('http');
            if (isUrl) {
                await this.installComponentByUrl(inputValue); // 需要新增此方法
            } else {
                await this.installComponent(inputValue);
            }

            this.toggleModal(false);
            componentUrlInput.value = '';
        });

        // 同步远程组件按钮事件
        document.getElementById('sync-components')?.addEventListener('click', async () => {
            // 模拟同步加载
            const syncBtn = document.getElementById('sync-components');
            syncBtn.disabled = true;
            syncBtn.textContent = '同步中...';

            try {
                // 实际项目中这里会调用接口获取最新组件列表
                await new Promise(resolve => setTimeout(resolve, 1500));
                alert('组件同步完成');
                this.renderComponentList(); // 重新渲染列表
                await saveLastSyncTime(Date.now());
            } catch (error) {
                console.error('同步失败:', error);
                alert('同步失败，请稍后重试');
            } finally {
                syncBtn.disabled = false;
                syncBtn.textContent = '同步远程组件';
            }
        });
    }

// 新增：模态框显示/隐藏控制方法
    toggleModal(show) {
        const modal = document.getElementById('installModal');
        if (modal) {
            modal.style.display = show ? 'flex' : 'none';
            modal.classList.toggle('active', show);
        }
    }

// 新增：通过URL安装组件的方法（示例）
    async installComponentByUrl(url) {
        try {
            // 实际项目中这里会验证URL并下载组件
            // 模拟一个组件ID（实际应从URL解析或接口返回）
            const mockComponentId = 'url-comp-' + Date.now().toString(36).slice(-6);

            // 检查是否已安装
            const isInstalled = this.installedComponents.some(c => c.id === mockComponentId);
            if (isInstalled) {
                alert('该组件已安装');
                return;
            }

            // 模拟组件信息（实际应从URL对应的资源获取）
            const newComponent = {
                id: mockComponentId,
                name: '自定义URL组件',
                description: '通过外部URL安装的组件',
                version: '1.0.0',
                size: '未知',
                updateTime: new Date().toISOString().split('T')[0],
                required: false
            };

            // 添加到组件列表和已安装列表
            this.components.push(newComponent);
            this.installedComponents.push({ ...newComponent, enabled: true });

            await saveInstalledComponents(this.installedComponents);
            this.renderComponentList();
            alert('组件安装成功');
        } catch (error) {
            console.error('URL安装失败:', error);
            alert(`安装失败: ${error.message}`);
        }
    }

    // 设置加载状态
    setLoading(isLoading) {
        this.isLoading = isLoading;
        const listEl = document.getElementById('componentList');
        const syncBtn = document.getElementById('sync-components');

        if (isLoading) {
            listEl.innerHTML = '<div style="text-align: center; padding: 2rem;">加载中...</div>';
            syncBtn.disabled = true;
        } else {
            syncBtn.disabled = false;
        }
    }

    // 渲染错误提示
    renderError(message) {
        const listEl = document.getElementById('componentList');
        listEl.innerHTML = `
      <div style="text-align: center; padding: 2rem; color: #e74c3c;">
        ${message}
        <button class="component-btn retry-btn">重试</button>
      </div>
    `;
        document.querySelector('.retry-btn').addEventListener('click', () => this.init());
    }
}

// 初始化函数
export const initComponentManager = async () => {
    const manager = new ComponentManager();
    await manager.init();
    return manager;
};